interface Branch {
  name: string
  protected: boolean
}

export function BranchSelectorInline() {
  const { selectedRepo, selectedBranch, setSelectedBranch } = useGitHubStore()
  const [branches, setBranches] = useState<Branch[]>([])
  const [loading, setLoading] = useState(false)

  const truncateText = (text: string, maxLength: number = 25) => {
    if (text.length <= maxLength) return text
    return text.substring(0, maxLength - 3) + '...'
  }

  useEffect(() => {
    if (selectedRepo) {
      fetchBranches()
    }
  }, [selectedRepo])

  const fetchBranches = async () => {
    if (!selectedRepo) return

    setLoading(true)
    try {
      const response = await fetch(
        `/api/github/branches?owner=${selectedRepo.owner}&repo=${selectedRepo.repo}`
      )
      const data = await response.json()

      if (response.ok && Array.isArray(data)) {
        setBranches(data)
      }
    } catch (err) {
      console.error('Failed to fetch branches:', err)
    } finally {
      setLoading(false)
    }
  }

  if (!selectedRepo) {
    return null
  }

  if (loading) {
    return (
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
        <GitBranchIcon sx={{ fontSize: 20, color: 'text.secondary' }} />
        <Typography variant="caption" color="text.secondary" fontWeight={600}>
          Branch:
        </Typography>
        <Box sx={{ py: 0.5, px: 1 }}>
          <CircularProgress size={20} />
        </Box>
      </Box>
    )
  }

  return (
    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
      <GitBranchIcon sx={{ fontSize: 20, color: 'text.secondary' }} />
      <Typography variant="caption" color="text.secondary" fontWeight={600}>
        Branch:
      </Typography>
      <Select
        value={selectedBranch}
        onChange={(e) => setSelectedBranch(e.target.value)}
        displayEmpty
        variant="standard"
        disableUnderline
        autoWidth
        renderValue={(value) => {
          if (!value) return <Typography variant="body2" color="text.secondary">Select...</Typography>
          const truncated = truncateText(value, 40)

          if (value !== truncated) {
            return (
              <Tooltip title={value} placement="bottom">
                <Typography variant="body2" fontWeight={600}>
                  {truncated}
                </Typography>
              </Tooltip>
            )
          }

          return (
            <Typography variant="body2" fontWeight={600}>
              {value}
            </Typography>
          )
        }}
        sx={{
          fontSize: '0.875rem',
          '&:hover': {
            bgcolor: 'action.hover',
            borderRadius: 1,
          },
          '& .MuiSelect-select': {
            py: 0.5,
            px: 1,
          },
        }}
      >
        {branches.map((branch, index) => (
          <Box key={branch.name}>
            {index > 0 && <Divider sx={{ my: 0.5 }} />}
            <MenuItem
              value={branch.name}
              sx={{
                py: 1,
                px: 2,
                '&:hover': {
                  bgcolor: 'action.hover',
                },
                '&.Mui-selected': {
                  bgcolor: 'primary.main',
                  color: 'primary.contrastText',
                  '&:hover': {
                    bgcolor: 'primary.dark',
                  },
                },
              }}
            >
              <Box sx={{ width: '100%' }}>
                <Typography variant="body2" fontWeight={500} noWrap>
                  {branch.name}
                  {branch.protected && ' '}
                </Typography>
              </Box>
            </MenuItem>
          </Box>
        ))}
      </Select>
    </Box>
  )
}
